"use client";

import * as React from "react";
import { cn } from "@/lib/utils";
import {
  Archive,
  ArchiveX,
  ChevronLeft,
  ChevronRight,
  Forward,
  MoreVertical,
  Reply,
  Search,
  Trash,
  Clock,
} from "lucide-react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Separator } from "@/components/ui/separator";
import { Tabs, TabsList, TabsTrigger } from "@/components/ui/tabs";
import {
  Tooltip,
  TooltipContent,
  TooltipProvider,
  TooltipTrigger,
} from "@/components/ui/tooltip";
import { AccountSwitcher } from "./account-switcher";
import { MailList } from "./mail-list";
import { MailDisplay } from "./mail-display";
import {
  defaultLayout as defaultLayoutConfig,
  defaultMails,
  mailCategories as mailCategoriesConfig,
  mailFolders as mailFoldersConfig,
  type Mail,
} from "../data";
import {
  ResizableHandle,
  ResizablePanel,
  ResizablePanelGroup,
} from "@/components/ui/resizable";

interface MailProps {
  defaultLayout?: number[];
  defaultCollapsed?: boolean;
  navCollapsedSize: number;
}

// Define default categories and folders in case the imported ones are undefined
const DEFAULT_CATEGORIES = [
  { name: "Inbox", count: 4 },
  { name: "Drafts", count: 0 },
  { name: "Sent", count: 0 },
  { name: "Junk", count: 0 },
  { name: "Trash", count: 0 },
];

const DEFAULT_FOLDERS = [
  { name: "Social", count: 0 },
  { name: "Updates", count: 0 },
  { name: "Forums", count: 0 },
];

export function Mail({
  defaultLayout,
  defaultCollapsed = false,
  navCollapsedSize,
}: MailProps) {
  // Ensure defaultLayout is properly initialized
  const layoutConfig = defaultLayout || defaultLayoutConfig || [20, 40, 40];

  // Initialize with fallbacks for categories and folders
  const categories = React.useMemo(() => {
    return mailCategoriesConfig || DEFAULT_CATEGORIES;
  }, []);

  const folders = React.useMemo(() => {
    return mailFoldersConfig || DEFAULT_FOLDERS;
  }, []);

  const [isCollapsed, setIsCollapsed] = React.useState(defaultCollapsed);
  const [mails] = React.useState<Mail[]>(defaultMails || []);
  const [selectedMail, setSelectedMail] = React.useState<Mail | null>(
    mails.length > 0 ? mails[0] : null,
  );
  const [mailFilter, setMailFilter] = React.useState<string>("all");

  return (
    <TooltipProvider delayDuration={0}>
      <ResizablePanelGroup
        direction="horizontal"
        onLayout={(sizes: number[]) => {
          document.cookie = `react-resizable-panels:layout:mail=${JSON.stringify(
            sizes,
          )}`;
        }}
        className="h-full items-stretch"
      >
        {/* Sidebar */}
        <ResizablePanel
          defaultSize={layoutConfig[0]}
          collapsedSize={navCollapsedSize}
          collapsible={true}
          minSize={15}
          maxSize={20}
          className={cn(
            isCollapsed &&
              "min-w-[50px] transition-all duration-300 ease-in-out",
          )}
        >
          <div className="flex h-full flex-col">
            {/* Account Switcher */}
            <div className="border-b px-4 py-2">
              <AccountSwitcher isCollapsed={isCollapsed} />
            </div>
            {/* Mail Categories */}
            <div className="flex-1 overflow-auto">
              {categories.map((category) => (
                <Button
                  key={category.name}
                  variant={category.name === "Inbox" ? "secondary" : "ghost"}
                  size="sm"
                  className={cn(
                    "w-full justify-start px-4 py-2",
                    category.name === "Inbox" && "bg-secondary",
                  )}
                >
                  <div className="flex w-full items-center gap-2">
                    {category.name}
                    {category.count > 0 && (
                      <span className="ml-auto text-xs font-medium">
                        {category.count}
                      </span>
                    )}
                  </div>
                </Button>
              ))}
              <Separator className="my-2" />
              {/* Mail Folders */}
              {folders.map((folder) => (
                <Button
                  key={folder.name}
                  variant="ghost"
                  size="sm"
                  className="w-full justify-start px-4 py-2"
                >
                  <div className="flex w-full items-center gap-2">
                    {folder.name}
                    {folder.count > 0 && (
                      <span className="ml-auto text-xs font-medium">
                        {folder.count}
                      </span>
                    )}
                  </div>
                </Button>
              ))}
            </div>
          </div>
        </ResizablePanel>

        <ResizableHandle withHandle />

        {/* Mail List */}
        <ResizablePanel defaultSize={layoutConfig[1]} minSize={30}>
          <div className="flex h-full flex-col">
            {/* Mail List Header */}
            <div className="border-b p-2">
              <div className="flex items-center justify-between">
                <h1 className="text-xl font-bold">Inbox</h1>
                <div className="flex items-center gap-2">
                  <Button variant="ghost" size="icon">
                    <Trash className="h-4 w-4" />
                  </Button>
                  <Button variant="ghost" size="icon">
                    <Archive className="h-4 w-4" />
                  </Button>
                  <Button variant="ghost" size="icon">
                    <ArchiveX className="h-4 w-4" />
                  </Button>
                  <Button variant="ghost" size="icon">
                    <Clock className="h-4 w-4" />
                  </Button>
                  <Button variant="ghost" size="icon">
                    <MoreVertical className="h-4 w-4" />
                  </Button>
                </div>
              </div>
              <div className="mt-2">
                <div className="relative">
                  <Search className="absolute left-2 top-2.5 h-4 w-4 text-muted-foreground" />
                  <Input className="pl-8" placeholder="Search" />
                </div>
              </div>
              <div className="mt-2">
                <Tabs defaultValue="all">
                  <TabsList className="w-full justify-start rounded-none border-b bg-transparent p-0">
                    <TabsTrigger
                      value="all"
                      className="rounded-none border-b-2 border-b-transparent px-4 py-2 data-[state=active]:border-b-primary data-[state=active]:bg-transparent"
                    >
                      All mail
                    </TabsTrigger>
                    <TabsTrigger
                      value="unread"
                      className="rounded-none border-b-2 border-b-transparent px-4 py-2 data-[state=active]:border-b-primary data-[state=active]:bg-transparent"
                    >
                      Unread
                    </TabsTrigger>
                  </TabsList>
                </Tabs>
              </div>
            </div>

            {/* Mail List */}
            <div className="flex-1 overflow-auto">
              <MailList
                items={mails}
                selectedMail={selectedMail}
                onSelectMail={setSelectedMail}
              />
            </div>
          </div>
        </ResizablePanel>

        <ResizableHandle withHandle />

        {/* Mail View */}
        <ResizablePanel defaultSize={layoutConfig[2]} minSize={30}>
          <div className="flex h-full flex-col">
            {/* Mail View Header */}
            <div className="flex items-center justify-between border-b p-2">
              <div className="flex items-center gap-2">
                <Button variant="ghost" size="icon">
                  <ChevronLeft className="h-4 w-4" />
                </Button>
                <Button variant="ghost" size="icon">
                  <ChevronRight className="h-4 w-4" />
                </Button>
                <Button variant="ghost" size="icon">
                  <Forward className="h-4 w-4" />
                </Button>
              </div>
              <div className="flex items-center gap-2">
                <Button variant="ghost" size="icon">
                  <MoreVertical className="h-4 w-4" />
                </Button>
              </div>
            </div>

            {/* Mail Content */}
            <div className="flex-1 overflow-auto">
              {selectedMail ? (
                <MailDisplay mail={selectedMail} />
              ) : (
                <div className="p-8 text-center text-muted-foreground">
                  No message selected
                </div>
              )}
            </div>
          </div>
        </ResizablePanel>
      </ResizablePanelGroup>
    </TooltipProvider>
  );
}
