"use client";

import * as React from "react";
import { format } from "date-fns";
import {
  Archive,
  ArchiveX,
  Clock,
  Forward,
  MoreVertical,
  Reply,
  ReplyAll,
  Trash,
} from "lucide-react";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardFooter,
  CardHeader,
} from "@/components/ui/card";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Separator } from "@/components/ui/separator";
import { Switch } from "@/components/ui/switch";
import { Textarea } from "@/components/ui/textarea";
import { type Mail } from "../data";
import { Badge } from "@/components/ui/badge";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";

interface MailDisplayProps {
  mail: Mail;
}

export function MailDisplay({ mail }: MailDisplayProps) {
  const [isReplying, setIsReplying] = React.useState(false);

  // Safely access mail properties
  const from = mail?.from || "Unknown Sender";
  const subject = mail?.subject || "No Subject";
  const message = mail?.message || "No message content";
  const date = mail?.date ? new Date(mail.date) : new Date();
  const read = mail?.read ?? true;
  const labels = mail?.labels && Array.isArray(mail.labels) ? mail.labels : [];

  // Format email for Reply-To
  const emailAddress = from.toLowerCase().replace(/\s/g, "") + "@example.com";

  // Get avatar initials
  const getInitials = (name: string) => {
    return name.charAt(0).toUpperCase();
  };

  return (
    <div className="flex h-full flex-col">
      <div className="flex items-center p-2">
        <div className="flex items-center gap-2">
          <Button variant="ghost" size="icon">
            <Archive className="h-4 w-4" />
          </Button>
          <Button variant="ghost" size="icon">
            <ArchiveX className="h-4 w-4" />
          </Button>
          <Button variant="ghost" size="icon">
            <Trash className="h-4 w-4" />
          </Button>
          <Separator orientation="vertical" className="mx-1 h-6" />
          <Button variant="ghost" size="icon">
            <Clock className="h-4 w-4" />
          </Button>
        </div>
        <div className="ml-auto flex items-center gap-2">
          <DropdownMenu>
            <DropdownMenuTrigger asChild>
              <Button variant="ghost" size="icon">
                <MoreVertical className="h-4 w-4" />
              </Button>
            </DropdownMenuTrigger>
            <DropdownMenuContent align="end">
              <DropdownMenuItem>Archive</DropdownMenuItem>
              <DropdownMenuItem>Move to junk</DropdownMenuItem>
              <DropdownMenuItem>Move to trash</DropdownMenuItem>
              <Separator className="my-2" />
              <DropdownMenuItem>Snooze</DropdownMenuItem>
            </DropdownMenuContent>
          </DropdownMenu>
        </div>
      </div>
      <Separator />
      <ScrollArea className="flex-1">
        <div className="p-6">
          {/* Mail header */}
          <div className="flex items-start gap-4">
            <Avatar className="h-10 w-10">
              <AvatarImage
                src={`https://ui.shadcn.com/avatars/${getInitials(from).toLowerCase()}.png`}
                alt={from}
              />
              <AvatarFallback>{getInitials(from)}</AvatarFallback>
            </Avatar>
            <div className="grid gap-1">
              <div className="font-semibold">{from}</div>
              <div className="line-clamp-1 text-sm">{subject}</div>
              <div className="text-xs text-muted-foreground">
                Reply-To: {emailAddress}
              </div>
              <div className="text-xs text-muted-foreground">
                {format(date, "PPP p")}
              </div>
            </div>
          </div>

          {/* Mail content */}
          <Separator className="my-6" />
          <div className="space-y-4 text-sm">
            <p>{message}</p>
            {isReplying && (
              <div className="mt-6 border-t pt-6">
                <div className="flex space-x-4">
                  <Avatar className="h-8 w-8">
                    <AvatarFallback>ME</AvatarFallback>
                  </Avatar>
                  <div className="grid w-full gap-4">
                    <Textarea
                      placeholder="Reply Mia Harris..."
                      className="min-h-[150px] resize-none p-4"
                    />
                    <div className="flex items-center justify-between">
                      <div className="flex items-center gap-2">
                        <Switch id="mute" />
                        <label htmlFor="mute" className="text-xs">
                          Mute this thread
                        </label>
                      </div>
                      <Button>Send</Button>
                    </div>
                  </div>
                </div>
              </div>
            )}
            {!isReplying && (
              <Button
                className="mt-6"
                variant="outline"
                onClick={() => setIsReplying(true)}
              >
                Reply
              </Button>
            )}
          </div>
        </div>
      </ScrollArea>
    </div>
  );
}
